import { Component, OnInit } from '@angular/core';
import { VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss']
})
export class VideoComponent implements OnInit {

  videos: any = [];
  constructor(
    private vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth
  ) {
  }

  ngOnInit(): void {
    this.vhAuth.getManualVideos('desktop')
      .then((manualVideos) => {
        if (!manualVideos.empty) {
          manualVideos.docs.forEach(video => {
            this.videos.push(this.vhAlgorithm.vhYoutubeLink(video.data().ytid));
          });
        }
      }, error => {
        console.error('error', error);
      })
  }

}
